import { FunctionComponent, useState } from 'react'
import styled from '@emotion/styled'

//icons
import MenuIcon from '@mui/icons-material/Menu'
import GitIcon from '@mui/icons-material/GitHub'
import InstaIcon from '@mui/icons-material/Instagram'
import InfoIcon from '@mui/icons-material/Info'
import PostIcon from '@mui/icons-material/Book'
import PortIcon from '@mui/icons-material/Person'

//meterial
import Box from '@mui/material/Box'
import Drawer from '@mui/material/Drawer'
import List from '@mui/material/List'
import Divider from '@mui/material/Divider'
import ListItem from '@mui/material/ListItem'
import ListItemIcon from '@mui/material/ListItemIcon'
import ListItemText from '@mui/material/ListItemText'
import { IconButton } from '@mui/material'
import { Link } from 'gatsby'

const HeaderWrapper = styled.header`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 52px;
  @media (max-width: 768px) {
    height: 40px;
  }
`

const BlogTitleWrapper = styled.div`
  font-size: 20px;
  font-weight: 800;
  padding-right: 20px;
  @media (max-width: 768px) {
    font-size: 15px;
  }
`

const Header: FunctionComponent = () => {
  const [state, setState] = useState(false)

  //side menu
  const toggleButton = () => {
    setState(!state)
  }

  const list = () => (
    <Box sx={{ width: 250 }} role="presentation">
      <List>
        {['블로그 소개', '게시글'].map((text, index) => (
          <ListItem
            button
            key={text}
            component={Link}
            to={index === 0 ? '/info' : ''}
          >
            <ListItemIcon>
              {index === 0 ? <InfoIcon /> : index === 1 ? <PostIcon /> : null}
            </ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
        ))}
      </List>
      <Divider />
      <List>
        {['Github', 'Instagram', 'Portfolio'].map((text, index) => (
          <ListItem
            button
            key={text}
            component={Link}
            to={
              text === 'Github'
                ? 'https://github.com/le2sky'
                : text === 'Instagram'
                ? 'https://www.instagram.com/lee.___.sky/'
                : text === 'Portfolio'
                ? '/404'
                : '/404'
            }
          >
            <ListItemIcon>
              {index === 0 ? (
                <GitIcon />
              ) : index === 1 ? (
                <InstaIcon />
              ) : index === 2 ? (
                <PortIcon />
              ) : null}
            </ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
        ))}
      </List>
    </Box>
  )
  return (
    <HeaderWrapper>
      <IconButton onClick={toggleButton}>{<MenuIcon />}</IconButton>
      <Drawer open={state} onClose={toggleButton}>
        {list()}
      </Drawer>
      <BlogTitleWrapper>Haneul Lee</BlogTitleWrapper>
    </HeaderWrapper>
  )
}

export default Header
